<template>
    <div>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    fixed
                    prop="id"
                    label="编号"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="书名"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="author"
                    label="作者"
                    width="120">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="100">
                <template slot-scope="scope">
                    <el-button @click="edit(scope.row)" type="text" size="small">修改</el-button>
                    <el-button @click="deleteBook(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
                background
                layout="prev, pager, next"
                :page-size="6"
                :total= "20"
                @current-change="page">
        </el-pagination>
    </div>

</template>

<script>
    export default {
        methods: {
            edit(row) {
                this.$router.push({
                    path: '/Update',
                    query:{
                        id:row.id
                    }
                });
               // row.id;
            },
            deleteBook(row){
                const _this =this
                axios.delete('http://localhost:8181/book/deleteById/'+row.id).then(function(resp){
                    _this.$alert('《'+row.name+'》 删除成功', '成功', {
                        confirmButtonText: '确定',
                        callback: action => {
                            //本来就在这个页面，所以push没用，还是需要动态刷新
                            //     _this.$router.push('/BookManage')
                            window.location.reload();

                        }
                    });
                })
            },

            page(currentPage){
                const _this=this
                axios.get('http://localhost:8181/book/findAll/'+currentPage+'/6').then(function(resp){
                    _this.tableData = resp.data;
                    // _this.totalElements = resp.data.totalElements;
                })
            },
        },

        created(){
            const _this=this;
            axios.get('http://localhost:8181/book/findAll/1/6').then(function(resp){
                _this.tableData = resp.data;
                // _this.totalElements = resp.data.length;
                console.log(_this.tableData);
            })
        },

        data() {
            return {
                totalElements: null,
                tableData: [{
                    id: '1',
                    name: '解忧杂货店',
                    author: '东野圭吾',

                }, {
                    id: '2',
                    name: '追风筝的人',
                    author: '卡勒德·胡赛尼',
                }, {
                    id: '3',
                    name: '人间失格',
                    author: '太宰治',
                }, {
                    id: '4',
                    name: '这就是二十四节气',
                    author: '高春香',
                }]
            }
        }
    }
</script>